#cityCard{
  display: block;
  width: 30%;
  height: 25%;
  margin: auto;
  margin-top: 30px;
  background-color: rgb(52,58,64);
  color: white;
 }
 
 #cityCard .card-title{
   font-size: 30px;
   text-align: center;
 }
 
 #cityCard .card-text {
   font-size: 20px;
   text-align: center;
 }
 
 @media only screen and (max-width: 750px) {
 
   #cityCard {
     width: 50%;
   }
 
   #cityCard .card-title{
     font-size: 25px;
   }
   
   #cityCard .card-text {
     font-size: 20px;
   }
 }

 #footer{
   position: fixed;
   bottom: 0;
   text-align: center;
 }